import { __ } from "@wordpress/i18n";
import "./editor.scss";
import { useEffect } from "@wordpress/element";
import InfoBox2 from "./components/InfoBox2";

export default function Edit({ attributes, setAttributes, context }) {
	const { content } = attributes;

	// ðŸ”¥ Parent attributes via context
	const infoBox = context["smartInfoBox/infoBox"];
	const container = context["smartInfoBox/container"];
	const mediaContainer = context["smartInfoBox/mediaContainer"];
	const title = context["smartInfoBox/title"];
	const description = context["smartInfoBox/description"];
	const cta = context["smartInfoBox/cta"];
	const featuredContainer = context["smartInfoBox/featuredContainer"];
	if (!attributes.blockId) {
		setAttributes({
			blockId: `smart-info-box-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`
		});
	}

	const handleTitleChange = (newTitle) => {
		setAttributes({
			content: {
				...content,
				title: newTitle,
			},
		});
	};

	const handleDescriptionChange = (newDescription) => {
		setAttributes({
			content: {
				...content,
				description: newDescription,
			},
		});
	};

	const handleButtonTextChange = (newButtonText) => {
		setAttributes({
			content: {
				...content,
				buttonText: newButtonText,
			},
		});
	};

	useEffect(() => {
		setAttributes({
			infoBox,
			container,
			mediaContainer,
			title,
			description,
			cta,
			featuredContainer,
		});
	}, [
		infoBox,
		container,
		mediaContainer,
		title,
		description,
		cta,
		featuredContainer,
	]);

	return (
		<>
			{/* <InspectorControls>
				<PanelBody title="Content">
					<TextControl
						label="Title"
						onChange={handleTitleChange}
						value={title}
					/>
					<br />
					<TextareaControl
						label={__("Description", "smart-info-box")}
						value={description}
						onChange={handleDescriptionChange}
					/>
					<br />
					<TextControl
						label={__("Button Text", "smart-info-box")}
						onChange={handleButtonTextChange}
						value={buttonText}
					/>
					<p>Icon</p>
					<IconSelect attributes={attributes} setAttributes={setAttributes} />
				</PanelBody>
			</InspectorControls> */}
			<InfoBox2
				attributes={attributes}
				setAttributes={setAttributes}
				context={context}
				handleTitleChange={handleTitleChange}
				handleDescriptionChange={handleDescriptionChange}
				handleButtonTextChange={handleButtonTextChange}
			/>
		</>
	);
}
